* {
  text-decoration: none;
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

body {
  background-color: #f1f0fe
}

html,
body,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  /*내부아이템의 부피에맞게 크기가 조절 됨.*/
  flex-wrap: wrap;
  padding: 50px;
  width: 100%;
}

.movies .movie {
  width: 40%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px 5px 5px 5px;
  border: 0px solid #000000;
  box-shadow: 4px 19px 20px -1px rgba(109, 122, 173, 0.12);
}

.movie:hover {
  transition: all 0.4s ease 0s;
  cursor: pointer;
  box-shadow: 4px 3px 5px -1px rgba(109, 122, 173, 0.3);
}

.movie img {
  position: relative;
  top: -50px;
  max-width: 140px;
  margin-right: 20px;

}

.movie__title {
  font-size: 20px;
  font-weight: 500;

}

.movie__year {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 14px;
  opacity: 0.4;
}

.movie__genres {
  margin-bottom: 10px;
  display: flex;
}

.movie__genres li {
  font-weight: 700;
  font-size: 14px;
  opacity: 0.4;
  color: #000000;
  margin-right: 10px;

}

